@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.App {
  height: 100vh;
  background-image: url(https://images.pexels.com/photos/34088/pexels-photo.jpg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
}

/* ---------- */

/* Header */

header {
  font-family: 'Open Sans Condensed', sans-serif;
}

header a {
  font-size: 20px !important;
}

header a , header a:hover {
  color: inherit;
  text-decoration: none !important;
  outline: none !important;
}

.topnav {
  width: 100%;
  justify-content: center;
}

.topnav a {
  color: white;
}
/* End of Header */

/* All */

.mainareas {
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0, 0.7);
  color: white;
  font-family: 'Open Sans Condensed', sans-serif;
  width: 100%;
  height: 92.5%;
}

@media (max-width: 375px) {
  .mainareas {
    height: 93.5%;
  }
}
/* End of All */

/* Main */

.mainpage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  padding: 20px;
}

.mainpage p {
  margin: 10px 0;
}

.logo {
  width: 15rem;
}

.myposition {
  font-size: 20px;
}

.mylinks {
  font-size: 25px;
}

.mylinks a {
  color: inherit;
  text-decoration: none;
  margin: 10px;
  transition: .5s;
}

/* End of Main */

/* About */

.aboutarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Old mobile */
@media (min-width: 320px) {
  .aboutarea img {
    width: 250px;
    height: 250px;
    object-fit: cover;
    margin-bottom: 10px;
  }

  .aboutarea p {
    font-size: 1rem;
    width: 250px;
  }
}

/* Iphone X */
@media (min-width: 375px) {
  .aboutarea img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    margin-bottom: 10px;
  }

  .aboutarea p {
    font-size: 1.3rem;
    width: 300px;
  }
}
/* End of About */

/* Skills */

.skillsarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skillsarea h1 {
  border-bottom: solid 1px white;
}

.skillsarea p {
  text-align: center;
}

.skillsarea ul {
  padding: 0;
  margin: 10px;
}

.skillsarea li {
  list-style: none;
}

.skills {
  display: flex;
  justify-content: space-evenly;
  border: solid 1px white;
  border-radius: 2px;
}

/* Old Mobile */
@media (min-width: 320px) {
  .skillsarea .jobs {
    width: 250px;
    margin-top: 10px;
    font-size: 12px;
  }
}

/* Iphone X */
@media (min-width: 375px) {
  .skillsarea p {
    width: 300px;
  }

  .skills {
    width: 300px;
  }

  .skillsarea p {
    font-size: 3vh;
  }

  .skillsarea li {
    font-size: 20px;
  }

  .skillsarea .jobs {
    margin-top: 16px;
    font-size: 18px;
  }
}

/* Large Screen */
@media (min-width: 1200px) {
  .skillsarea p {
    width: 500px;
  }

  .skills {
    width: 500px;
  }
}

/* End of Skills */

/* Projects */

.projectareas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.singleproject h1 {
  border-bottom: solid 1px white;
}

.projectinfo {
  display: flex;
}

.projecttext {
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.singleproject a {
  color: deepskyblue;
  transition: .5s;
}

.singleproject button {
  color: white;
  background-color: rgb(52, 74, 110);
  transition: .5s;
}

.singleproject button:hover {
  background-color: rgb(75, 110, 165);
}

/* Old Mobile */
@media (min-width: 320px) {
  .singleproject {
    width: 250px;
    height: 160px;
    margin: 0;
  }

  .singleproject h1 {
    font-size: 1rem;
  }

  .singleproject img {
    width: 80px;
    position: relative;
  }

  .projecttext p {
    font-size: 10px;
  }
}

/* Iphone X */
@media (min-width: 375px) {
  .singleproject {
    width: 300px;
    height: 240px;
    margin: 0;
  }

  .singleproject h1 {
    font-size: 2.5rem;
  }

  .singleproject img {
    width: 100px;
    position: relative;
  }

  .singleproject .buttonlink  {
    position: relative;
    right: 100px;
    bottom: 60px;
  }

  .projectinfo p {
    font-size: 2.2vh;
  }
}

/* Large Screen */
@media (min-width: 1024px) {
  .singleproject {
    width: 700px;
    height: auto;
    margin: 15px;
  }

  .singleproject img {
    width: 200px;
    border: solid 1px white;
  }

  .singleproject .buttonlink {
    position: static;
  }

  .projectinfo p {
    font-size: 1rem;
  }
}

/* End of Projects */

/* Contact */

.formpage {
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.formpage p, h3 {
  text-align: center;
}

.formpage a {
  color: deepskyblue;
  transition: .5s;
}

.formpage form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.formpage button {
  color: white;
  background-color: rgb(52, 74, 110);
  transition: .5s;
}

.formpage button:hover {
  background-color: rgb(75, 110, 165);
}

/* Older Mobile */
@media (min-width: 320px) {
  .formpage {
    width: 275px;
  }

  .formpage p {
    font-size: 1rem;
  }
}

/* Iphone X */
@media (min-width: 375px) {
  .formpage {
    width: 300px;
  }

  .formpage p {
    font-size: 1.5rem;
  }
}

/* Large Screen */
@media (min-width: 1024px) {
  .formpage {
    width: 500px;
  }

  .formpage p {
    font-size: 22px;
  }
}

/* End of Contact */
/* HOVER FOR NAV */
div[class*=box] {
	height: 33.33%;
	width: 100%; 
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-1 { background-color: #FF6766; }

.btn {
	font-size: 24px;
	color: white !important;
	cursor: pointer;
	margin: 0 10px;
}

/* 
========================
      BUTTON ONE
========================
*/
.btn-one {
	color: #FFF;
	transition: all 0.3s;
	position: relative;
}
.btn-one span {
	transition: all 0.3s;
}
.btn-one::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 1px;
	border-bottom-width: 1px;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: rgba(255,255,255,0.5);
	border-bottom-color: rgba(255,255,255,0.5);
	transform: scale(0.1, 1);
}
.btn-one:hover span {
	letter-spacing: 2px;
}
.btn-one:hover::before {
	opacity: 1;	
	transform: scale(1, 1);	
}
.btn-one::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.3s;
}
.btn-one:hover::after {
	opacity: 0;	
	transform: scale(0.1, 1);
}

/* Contact Hover */

.contact {
	transition: .5s;
}

